/**
 *   
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-input
          size="small"
          v-model="formInline.rolename"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          size="small"
          v-model="formInline.roleid"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          size="small"
          type="primary"
          icon="el-icon-search"
          @click="search"
          ></el-button
        >
        <el-button
          size="small"
          type="primary"
          icon="el-icon-plus"
          @click="handleEdit()"
          ></el-button
        >
      </el-form-item>
    </el-form>
    <!---->
    <el-table
      size="small"
      :data="listData"
      highlight-current-row
      v-loading="loading"
      border
      element-loading-text=""
      style="width: 100%"
    >
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column
        sortable
        prop="userrealname"
        label=""
        width="300"
      >
      </el-table-column>
      <el-table-column sortable prop="roleid" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="edittime" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="edituser" label="" width="300">
      </el-table-column>
      <el-table-column align="center" label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="deleteUser(scope.$index, scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination
      v-bind:child-msg="pageparm"
      @callFather="callFather"
    ></Pagination>
    <!--  -->
    <el-dialog
      :title="title"
      :visible.sync="editFormVisible"
      width="30%"
      @click="closeDialog('edit')"
    >
      <el-form
        label-width="120px"
        :model="editForm"
        ref="editForm"
        :rules="rules"
      >
        <el-form-item label="" prop="rolename">
          <el-input
            size="small"
            v-model="editForm.rolename"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleid">
          <el-input
            size="small"
            v-model="editForm.roleid"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog('edit')"></el-button>
        <el-button
          size="small"
          type="primary"
          :loading="loading"
          class="title"
          @click="submitForm('editForm')"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { httpget, httppost } from "../../api/userMG";
import Pagination from "../../components/Pagination";
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      menuAccessshow: false, //
      title: "",
      editForm: {
        roleid: "",
        rolename: "",
        token: localStorage.getItem("logintoken"),
      },
      // rules 
      rules: {
        roleNo: [
          { required: true, message: "", trigger: "blur" },
        ],
        roleName: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      formInline: {
        page: 1,
        limit: 10,
        rolename: "",
        roleid: "",
        token: localStorage.getItem("logintoken"),
      },
      // 
      seletedata: {
        ids: "",
        token: localStorage.getItem("logintoken"),
      },
      userparm: [], //
      listData: [], //
      // 
      RoleRight: [],
      RoleRightProps: {
        children: "children",
        label: "name",
      },
      // 
      checkmenu: [],
      //role
      saveroleId: "",
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10,
      },
    };
  },
  // 
  components: {
    Pagination,
  },
  /**
   * 
   */

  watch: {},

  /**
   * 
   */

  created() {
    this.getdata(this.formInline);
  },

  /**
   * 
   */

  methods: {
    getRoles(id) {
      return new Promise((resolve, reject) => {
        var geturl =
          id > -1
            ? "/aa/crm/system-role/getrole?roleid=" + id
            : "/aa/crm/system-role/getroles";
        // console.log("geturl :::: " ,geturl);
        httpget(geturl).then((res) => {
          //console.log("promise ::: ",res);
          resolve(res);
        }, reject);
      });
    },

    // 
    getdata(parameter) {
      /***
       *  
       */
      //console.log("parameter ::: " ,parameter);
      var geturl =
        parameter.rolename === "" && parameter.roleid === ""
          ? "/aa/crm/system-role/getroles?limit=" +
            parameter.limit +
            "&page=" +
            parameter.page
          : "/aa/crm/system-role/searchrolelist?limit=" +
            parameter.limit +
            "&page=" +
            parameter.page +
            "&rolename=" +
            parameter.rolename +
            "&roleid=" +
            parameter.roleid;
      //console.log("geturl ::: ",geturl);
      httpget(geturl)
        .then((res) => {
          this.loading = false;
          if (res.code == 0) {
            this.$message({
              type: "success",
              message: res.msg,
            });
          } else {
            this.$message({
              type: "error",
              message: res.msg,
            });
            this.formInline.page = 1;
          }
          this.listData = res.roles;
          // 
          this.pageparm.currentPage = this.formInline.page;
          this.pageparm.pageSize = this.formInline.limit;
          this.pageparm.total = res.total;
        })
        .catch((err) => {
          this.loading = false;
          this.$message.error("");
        });
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage;
      this.formInline.limit = parm.pageSize;
      this.getdata(this.formInline);
    },
    // 
    search() {
      this.getdata(this.formInline);
    },
    //
    handleEdit: function (index, row) {
      this.editFormVisible = true;
      if (row != undefined && row != "undefined") {
        this.title = "";
        this.editForm.roleid = row.roleid;
        this.editForm.rolename = row.userrealname;
      } else {
        this.title = "";
        this.editForm.roleid = "";
        this.editForm.rolename = "";
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate((valid) => {
        if (valid) {
          var parm = JSON.stringify(this.editForm);
          console.log("editData :::: ", parm);
          httppost("/aa/crm/system-role/changerole", parm)
            .then((res) => {
              this.editFormVisible = false;
              this.loading = false;
              if (res.code == 0) {
                this.getdata(this.formInline);
                this.$message({
                  type: "success",
                  message: "",
                });
              } else {
                this.$message({
                  type: "info",
                  message: res.msg,
                });
              }
            })
            .catch((err) => {
              this.editFormVisible = false;
              this.loading = false;
              this.$message.error("");
            });
        } else {
          return false;
        }
      });
    },
    // 
    deleteUser(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          httppost("/aa/crm/system-role/delrole", row.roleid)
            .then((res) => {
              if (res.code == 0) {
                this.$message({
                  type: "success",
                  message: "",
                });
                this.getdata(this.formInline);
              } else {
                this.$message({
                  type: "info",
                  message: res.msg,
                });
              }
            })
            .catch((err) => {
              this.loading = false;
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },

    // 
    changemenu(arr) {
      let change = [];
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].checked) {
          change.push(arr[i].id);
        }
      }
      this.checkmenu = change;
    },

    // 
    closeDialog(dialog) {
      if (dialog == "edit") {
        this.editFormVisible = false;
      } else if (dialog == "perm") {
        this.menuAccessshow = false;
      }
    },
  },
};
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 


