/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-select
          size="small"
          v-model="formInline.isLock"
          placeholder=""
        >
          <el-option label="" value=""></el-option>
          <el-option label="" value="N"></el-option>
          <el-option label="" value="Y"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input
          size="small"
          v-model="formInline.userName"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          size="small"
          v-model="formInline.userMobile"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          size="small"
          type="primary"
          icon="el-icon-search"
          @click="search"
          ></el-button
        >
        <el-button
          size="small"
          type="primary"
          icon="el-icon-plus"
          @click="handleEdit()"
          ></el-button
        >
      </el-form-item>
    </el-form>
    <!---->
    <el-table
      size="small"
      @selection-change="selectChange"
      :data="userData"
      highlight-current-row
      v-loading="loading"
      border
      element-loading-text=""
      style="width: 100%"
    >
      <el-table-column align="center" type="selection" width="50">
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="deptname"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="username"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="userrealname"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="usermobile"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="usersex"
        label=""
        min-width="50"
      >
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="useremail"
        label=""
        min-width="120"
      >
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="edittime"
        label=""
        min-width="120"
      >
      </el-table-column>
      <el-table-column
        align="center"
        sortable
        prop="islock"
        label=""
        min-width="50"
      >
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.islock == 0 ? nshow : fshow"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="editType(scope.$index, scope.row)"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="deleteUser(scope.$index, scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="success"
            @click="resetpwd(scope.$index, scope.row)"
            ></el-button
          >

          <el-button
            size="mini"
            type="success"
            @click="offlineUser(scope.$index, scope.row)"
          >
            <div v-if="scope.row.isonline == 0">
              {{ (isonlinebuttontext = "") }}
            </div>
            <div v-if="scope.row.isonline == 1">
              {{ (isonlinebuttontext = "") }}
            </div>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination
      v-bind:child-msg="pageparm"
      @callFather="callFather"
    ></Pagination>
    <!--  -->
    <el-dialog
      :title="title"
      :visible.sync="editFormVisible"
      width="30%"
      @click="closeDialog('edit')"
    >
      <el-form
        label-width="80px"
        ref="editForm"
        :model="editForm"
        :rules="rules"
      >
        <el-form-item label="" prop="username">
          <el-input
            size="small"
            v-model="editForm.username"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="deptname">
          <!-- <el-input size="small" v-model="editForm.deptname" auto-complete="off" placeholder=""></el-input> -->
          <!-- <el-autocomplete
          v-model="autocompleteDeptName"
          :fetch-suggestions="autocompleteQuerySearchAsync"
          placeholder=""
          @select="autocompleteHandleSelect"
        ></el-autocomplete> -->
          <el-select
            size="small"
            v-model="editForm.deptid"
            placeholder=""
            class="userRole"
          >
            <el-option
              v-for="(dept, index) in depts"
              :key="index"
              :label="dept.deptname"
              :value="dept.deptid"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="roleid">
          <el-select
            size="small"
            v-model="editForm.roleid"
            placeholder=""
            class="userRole"
          >
            <el-option
              v-for="(rolea, index) in roles"
              :key="index"
              :label="rolea.userrealname"
              :value="rolea.roleid"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="usermobile">
          <el-input
            size="small"
            v-model="editForm.usermobile"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="useremail">
          <el-input
            size="small"
            v-model="editForm.useremail"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="usersex">
          <el-radio v-model="editForm.usersex" label=""></el-radio>
          <el-radio v-model="editForm.usersex" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog('edit')"></el-button>
        <el-button
          size="small"
          type="primary"
          :loading="loading"
          class="title"
          @click="submitForm('editForm')"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 
import { httpget, httppost } from "../../api/userMG";
import Pagination from "../../components/Pagination";
import { timestampToTime } from "../../utils/util";
export default {
  data() {
    return {
      isonlinebuttontext: "",
      autocompleteDeptName: "",
      depts: [],
      roles: [],
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      title: "",
      editFormVisible: false, //
      dataAccessshow: false, //
      unitAccessshow: false, //
      // 
      editForm: {
        userid: "",
        username: "",
        deptid: "",
        roleid: "",
        usermobile: "",
        useremail: "",
        usersex: "",
        token: localStorage.getItem("logintoken"),
      },
      // 
      unitparm: {
        userIds: "",
        deptId: "",
        deptName: "",
      },
      // 
      selectdata: [],
      // rules
      rules: {
        userName: [
          { required: true, message: "", trigger: "blur" },
        ],
        userRealName: [
          { required: true, message: "", trigger: "blur" },
        ],
        roleId: [{ required: true, message: "", trigger: "blur" }],
        userMobile: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^1(3\d|47|5((?!4)\d)|7(0|1|[6-8])|8\d)\d{8,8}$/,
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        userEmail: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        userSex: [{ required: true, message: "", trigger: "blur" }],
      },
      // 
      seletedata: {
        ids: "",
        token: localStorage.getItem("logintoken"),
      },
      // 
      resetpsd: {
        userId: "",
        token: localStorage.getItem("logintoken"),
      },
      // 
      offline: {
        token: localStorage.getItem("logintoken"),
      },
      // 
      formInline: {
        page: 1,
        limit: 10,
        deptId: "",
        userName: "",
        userMobile: "",
        isLock: "",
      },
      //
      userData: [],
      //role
      saveroleId: "",
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10,
      },
    };
  },
  // 
  components: {
    Pagination,
  },

  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.getdata(this.formInline);
  },

  /**
   * 
   */
  methods: {
    autocompleteQuerySearchAsync(queryString, cb) {
      console.log("queryString ::: ", queryString);
      var restaurants = this.depts;
      var results = queryString
        ? restaurants.filter(this.createStateFilter(queryString))
        : restaurants;
      console.log("results ::: ", results);
      // clearTimeout(this.timeout);
      // this.timeout = setTimeout(() => {
      //   cb(results);
      // }, 1000 * Math.random());
    },
    createStateFilter(queryString) {
      return (autocompleteDeptName) => {
        return autocompleteDeptName.value.indexOf(queryString) === 0;
      };
    },
    autocompleteHandleSelect(item) {
      console.log(item);
    },

    testdataget(parm) {
      return new Promise((resolve, reject) => {
        var geturl =
          "/aa/crm/system-user-manage/getuserlist?starts=" +
          parm.page +
          "&limit=" +
          parm.limit;
        if (parm.isLock != "" || parm.userName != "" || parm.userMobile != "") {
          geturl =
            "/aa/crm/system-user-manage/searchuserlist?starts=" +
            parm.page +
            "&limit=" +
            parm.limit +
            "&islock=" +
            parm.isLock +
            "&username=" +
            parm.userName +
            "&usermobile=" +
            parm.userMobile;
        }

        // console.log("geturl :::: " ,geturl);
        httpget(geturl).then((res) => {
          resolve(res);
        }, reject);
      });
    },

    getDepts(id, limit, starts) {
      return new Promise((resolve, reject) => {
        var geturl =
          id > -1
            ? "/aa/crm/system-dept/getdept?deptid=" + id
            : "/aa/crm/system-dept/getdepts";
        if (limit > -1 && starts > -1) {
          geturl =
            "/aa/crm/system-dept/getdeptlist?limit=" +
            limit +
            "&starts=" +
            starts;
        }

        console.log("geturl :::: ", geturl);
        httpget(geturl).then((res) => {
          //console.log("promise ::: ",res);
          resolve(res);
        }, reject);
      });
    },

    getRoles(id) {
      return new Promise((resolve, reject) => {
        var geturl =
          id > -1
            ? "/aa/crm/system-role/getrole?roleid=" + id
            : "/aa/crm/system-role/getroles";
        // console.log("geturl :::: " ,geturl);
        httpget(geturl).then((res) => {
          //console.log("promise ::: ",res);
          resolve(res);
        }, reject);
      });
    },

    // 
    async getdata(parameter) {
      this.loading = true;
      try {
        // console.log("parameter :::: " ,parameter)
        const data = await this.testdataget(parameter);
        console.log("getdata ::: " + data);
        if (data.code == 0) {
          // 
          this.loading = false;
          // console.log("data.data  ::: ", data.data);
          this.userData = data.data;

          // console.log("userdata ::: " ,this.userData);
          // 
          this.pageparm.currentPage = this.formInline.page;
          this.pageparm.pageSize = this.formInline.limit;
          this.pageparm.total = data.total;
          // 
        } else {
          this.$message({
            type: "info",
            message: data.msg,
          });
        }
        this.loading = false;
      } catch (error) {
        // 
      }
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage;
      this.formInline.limit = parm.pageSize;
      this.getdata(this.formInline);
    },
    //
    search() {
      this.getdata(this.formInline);
      // console.log("this.formInline ::: ",this.formInline);
    },
    // type
    editType: function (index, row) {
      this.loading = true;
      var parm = {
        lock: "",
        userid: "",
      };
      console.log("row ::: ", row);
      parm.userid = row.userid;
      parm.lock = row.islock == 0 ? 1 : 0;
      parm = JSON.stringify(parm);
      console.log("parm ::: ", parm);

      // 
      httppost("/aa/crm/system-user-manage/changelock", parm).then((res) => {
        console.log("res ::: ", res);
        if (res.code != 0) {
          this.$message({
            type: "info",
            message: res.msg,
          });
        } else {
          this.$message({
            type: "success",
            message: "",
          });
          this.getdata(this.formInline);
        }
      });
      this.loading = false;
    },
    //
    handleEdit: async function (index, row) {
      try {
        const a = await this.getRoles(-1);
        const b = await this.getDepts(-1, -1, -1);
        this.roles = a.roles;
        this.depts = b.depts;
      } catch (err) {
        console.log(" try error ::: ", err);
      }
      //this.$forceUpdate()
      console.log("roles ::: ", this.roles);
      // console.log("index ::: ",index , " -- row ::: ",row);

      this.editFormVisible = true;
      if (row != undefined && row != "undefined") {
        this.title = "";
        this.editForm.userid = row.userid;
        this.editForm.username = row.username;
        this.editForm.deptid = row.deptid;
        this.editForm.roleid = row.roleid;
        this.editForm.usermobile = row.usermobile;
        this.editForm.useremail = row.useremail;
        this.editForm.usersex = row.usersex;
      } else {
        this.title = "";
        this.editForm.userid = "";
        this.editForm.username = "";
        this.editForm.deptid = "";
        this.editForm.roleid = "";
        this.editForm.usermobile = "";
        this.editForm.useremail = "";
        this.editForm.usersex = "";
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate((valid) => {
        if (valid) {
          // 
          console.log("this.editForm 1::: ", this.editForm);

          this.editForm.usersex = this.editForm.usersex === "" ? 1 : 0;
          const bbbb = JSON.stringify(this.editForm);
          console.log("bbbb::: ", bbbb);
          httppost("aa/crm/system-user-manage/changeuser", bbbb).then((r) => {
            this.editFormVisible = false;
            this.loading = false;
            console.log("r ::: ", r);
            if (r.code == 0) {
              this.getdata(this.formInline);
              this.$message({
                type: "success",
                message: "",
              });
            } else {
              this.$message({
                type: "info",
                message: r.msg,
              });
            }
          });
        } else {
          return false;
        }
      });
    },

    // 
    selectChange(val) {
      this.selectdata = val;
    },
    // 
    closeDialog(dialog) {
      if (dialog == "edit") {
        this.editFormVisible = false;
      } else if (dialog == "perm") {
        this.dataAccessshow = false;
      } else if (dialog == "unit") {
        this.unitAccessshow = false;
      }
    },
    // 
    deleteUser(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          var parm = {
            userid: row.userid,
          };
          parm = JSON.stringify(parm);
          httppost("/aa/crm/system-user-manage/deluser", parm)
            .then((r) => {
              if (r.code == 0) {
                this.$message({
                  type: "success",
                  message: "!",
                });
                this.getdata(this.formInline);
              } else {
                this.$message({
                  type: "info",
                  message: res.msg,
                });
              }
            })
            .catch((err) => {
              this.loading = false;
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    resetpwd(index, row) {
      var parm = {
        userid: row.userid,
      };
      parm = JSON.stringify(parm);
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          httppost("/aa/crm/system-user-manage/deluserpwd", parm)
            .then((res) => {
              if (res.code == 0) {
                this.$message({
                  type: "success",
                  message: "",
                });
                this.getdata(this.formInline);
              } else {
                this.$message({
                  type: "info",
                  message: res.msg,
                });
              }
            })
            .catch((err) => {
              this.loading = false;
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    offlineUser(index, row) {
      var onlinetext = this.isonlinebuttontext;
      this.$confirm(
        " [ " + row.username + " ] " + onlinetext + "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(() => {
          var parm = {
            userid: row.userid,
            online: row.isonline == 0 ? 1 : 0,
          };
          parm = JSON.stringify(parm);
          httppost("/aa/crm/system-user-manage/changeonline", parm)
            .then((res) => {
              if (res.code == 0) {
                this.$message({
                  type: "success",
                  message:
                    " [ " +
                    row.username +
                    " ] " +
                    onlinetext +
                    "",
                });
                this.getdata(this.formInline);
              } else {
                this.$message({
                  type: "info",
                  message: res.msg,
                });
              }
            })
            .catch((err) => {
              this.loading = false;
              this.$message.error(
                " [ " + row.username + " ] ",
                err
              );
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 


